<template>
  <div class="container">
    <div class="content" v-html="introduce"></div>
    <div
      class="social"
      v-for="(item, index) in socialArr"
      @click="selectSocialPlatform(index)"
      :key="index"
    >
      <div class="socialItem">{{ item }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "About",
  components: {},
  data() {
    return {
      introduce:
        "<p><h1 style='color:#000'>Hello,World!</h1></p> <p><h3> This is Evan </br>I'm a Web / iOS Developer in Shenzhen,China</p><p>I can communicate in Code, Mandarin, English and Japanese</h3></p><p>Find me on<p>",
      socialArr: ["Github", "Twitter", "Mail"],
    };
  },
  computed: {},
  methods: {
    selectSocialPlatform(index) {
      var tempwindow, emailAddress;
      switch (index) {
        case 0:
          tempwindow = window.open("_blank");
          tempwindow.location = "https://github.com/evancohe";
          break;
        case 1:
          tempwindow = window.open("_blank");
          tempwindow.location = "https://twitter.com/evancohe";
          break;
        case 2:
          emailAddress = "evsdevsubs@outlook.com";
          location = `mailto:${emailAddress}?subject=hello&subject=Hi,Evan&body=`;
          break;
        default:
          break;
      }
    },
  },
};
</script>
<style lang='scss' scoped>
.content {
  text-align: left;
}
.social {
  display: inline-flex;
  padding: 10px 20px;
  flex-direction: row;
  .socialItem {
    background-color: aqua;
  }
}
</style>